body {
  font-family: monospace;
}

#gameScreen {
  background-color: black;
  border: 1px solid tomato;
}

img {
  display: none;
}

h1 {
  font-weight: bold;
}

ul {
  list-style-image: url("./assets/images/bricks.png");
}

ul li {
  margin-top: -2px;
}

.flex {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  text-align: center;
}

.flex button {
  width: 30px;
  background-color: black;
  border: none;
  color: white;
  font-weight: bold;
  padding: 8px 0px;
  cursor: pointer;
}

.flex button:hover {
  background-color: rgb(58, 58, 58);
}
